    }

    .web-search-icon-wrapper {
        height: 24px;
        display: flex;
        align-items: center;
        gap: 4px;
        border-radius: 4px;
        cursor: pointer;
        transition: all 0.3s ease;
        min-width: 24px; // 
        overflow: hidden; // 

        svg {
            flex-shrink: 0; //  svg 
        }

        .search-text {
            font-size: 12px;
            color: var(--el-color-primary);
            white-space: nowrap;
            opacity: 0;
            max-width: 0;
            transition: all 0.3s ease;
        }

        &.selected {
            svg {
                color: rgb(2 133 255);
                opacity: 1;
                pointer-events: none;

                &.rag {
                    color: var(--el-color-danger);
                }
