            background-color: var(--el-color-primary-light-9);
            padding: 0 8px 0 0;
        }

        .search-text {
            opacity: 1;
            max-width: 100px;
            font-size: 12px;
            font-weight: bold;
            color: rgb(2 133 255);

            &.rag {
                color: var(--el-color-danger);
            }
        }
    }
}

/**
