<template>
    <div class="common-layout">
        <el-container>
            <!--  -->
            <div class="overlay overlay-bg" v-if="!active" @click="toggleSidebar"></div>
            <!--  Start-->
            <el-aside class="aside-bg" :class="{ 'no-border': active }">
                <app-aside @toggle-sidebar="toggleSidebar" />
            </el-aside>
            <!--  End-->

            <!--   Start -->
            <div class="vertical-divider" @click="toggleSidebar" :class="{ 'active': active }">
                <SvgIcon icon-class="vertical-divider" />
            </div>
            <!--   End -->

            <!--  Start -->
            <el-container>
                <el-header class="header-bg">
                    <app-header />
                </el-header>
                <el-container class="main-bg">
                    <!--  -->
                    <el-main>
                        <router-view />
                    </el-main>
                    <!--  -->
                    <el-footer>
                        <app-footer />
                    </el-footer>
                </el-container>
            </el-container>
            <!--  End -->
        </el-container>
    </div>
</template>

<script>
import AppAside from '../views/AppAside.vue'
import AppHeader from '../views/AppHeader.vue'
import AppFooter from '../views/AppFooter.vue'

export default {
    name: 'Layout',
    data() {
        return {
            // 
            active: false,
            // 
            asideWidth: '250px'
        }
    },
    components: {
        AppAside,
        AppFooter,
        AppHeader
    },
    created() {
        this.handleResize()
    },
    mounted() {
        // 
        window.addEventListener('resize', this.handleResize);
    },
    methods: {
        /**
         * /
         *  active 
         */
        toggleSidebar() {
            this.active = !this.active;
            this.asideWidth = this.asideWidth === '0px' ? '250px' : '0px';
            // CSS
            document.documentElement.style.setProperty('--sidebar-width', this.asideWidth);
        },

        /**
         * 
         * 
         * (<=767px),
         */
        handleResize() {
            // XS
            if (window.matchMedia('(max-width: 767px)').matches) {
                // XS
                this.active = true
                this.asideWidth = '0px'
            } else {
                this.active = false
                this.asideWidth = '250px'
            }
            document.documentElement.style.setProperty('--sidebar-width', this.asideWidth);
        }
    }
}

</script>

<style lang="scss" scoped>
/**
 * 
 */
$animation-time: 0.3s; // 
$vertical-divider-width: 30px; // 

/**
 * 
 */
.common-layout {
    height: 100%;
    background-size: cover;
    background: var(--layout-common-layout-bg);

    >.el-container {
        height: 100%;

        >.el-aside {
            width: var(--sidebar-width);
            height: 100%;
            overflow-y: hidden;
            transition: all $animation-time;
            border-right: 1px solid var(--app-small-border-color);
            scrollbar-width: none;

            &::-webkit-scrollbar {
                display: none;
            }

            &.no-border {
                border: none;
            }
        }

        >.el-container {
            height: 100%;
            display: flex;
            flex-direction: column;

            >.el-container {
                flex: 1;
                overflow: hidden;
            }
        }
    }
}

.el-header {
    height: 45px;
    padding: 0;
    border-bottom: 1px solid var(--app-small-border-color);
}

.el-main {
    scrollbar-width: none;
    padding: 0;
    flex: 1;
    overflow-y: auto;

    &::-webkit-scrollbar {
        display: none;
    }
}

.el-footer {
    color: grey;
    text-align: center;
    height: 92px;
}

/**
 * 
 */
.vertical-divider {
    position: absolute;
    left: calc(var(--sidebar-width) - $vertical-divider-width / 2 + 10px);
    /*  */
    top: 40%;
    transform: translateY(-50%);
    width: $vertical-divider-width;
    cursor: pointer;
    display: flex;
    z-index: 100;
    transition: all $animation-time;

    >svg {
        width: $vertical-divider-width;
        height: $vertical-divider-width;
        transform: rotate(0deg);
        fill: var(--app-small-border-color);
    }

    &.active>svg {
        transform: rotate(180deg);
    }
}

/*  xs  */
@media (max-width: 767px) {

    // 
    .el-aside {
        position: absolute;
        width: 100%;
        z-index: 100;
    }
}

/*  */
.overlay {
    display: none; // 
}

/*  xs  */
@media (max-width: 767px) {

    // 
    .overlay {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 99; // 
    }
}
</style>
